import React from "react";
import ReactJoyride from "react-joyride";

interface Props {
    enabled: boolean;
    finishedCallback: any;
}

export class Tour extends React.Component<Props> {
    static STEPS = [
        {
            target: '#main_section',
            content: 'Generator pozwala Ci stworzyć grafikę do publikacji w mediach społecznościowych z danymi z dowolnej gminy.',
            disableBeacon: true,
        },
        {
            target: '.predefiniowane',
            content: 'Możesz skorzystać z już zdefiniowanych infografik. Wybierz temat, który Cię interesuje.',
        },
        {
            target: '.acc-udostepnij',
            content: 'Wybierz swoją gminę, powiat lub całe województwo i wygeneruj infografikę z wybranego szablonu. Plikiem możesz podzielić się np. na Facebooku lub Instagramie. Wygenerujemy też krótki link pod którym inni będą mogli ją znaleźć.',
        },
        {
            target: '.acc-ustawienia',
            content: 'Tutaj możesz zmienić tło i logo, wyedytować tekst i podpisać Twoją unikalną infografikę. Jeśli z kimś jeszcze nad nią pracujesz to możesz tutaj też skopiować link do wersji roboczej i wysłać ją mailem lub komunikatorem.',
        },
        {
            target: '.acc-komentarz',
            content: 'W tym miejscu dodasz komentarz (Twój, eksperta lub polityka) do infografiki.',
        },
        {
            target: '.acc-budzet',
            content: 'Tutaj włączysz lub wyłączysz pokazywanie modułów dotyczących budżetu danej gminy oraz ustawisz ich opcje.',
        },
        {
            target: '.acc-wykresy',
            content: 'A tutaj możesz dodać lub usunąć wykres. Pobierze on automatycznie dane z GUS, albo łatwo możesz wkleić własne.',
        },
        {
            target: '.acc-dodatkowe',
            content: 'Chcesz wiedzieć więcej o wybranej jednostce samorządu? Kliknij tutaj aby otworzyć dodatkowe dane w nowej zakładce.',
        },
        {
            target: '#start-tour',
            content: 'Chcesz skorzystać z samouczka jeszcze raz? Kliknij tutaj. Klik, klik.',
        },
    ]

    render () {
        const { enabled, finishedCallback } = this.props;
        // @ts-ignore
        const callback = data => {
            const { action } = data;
            if (action === "reset" || action === "close") {
                finishedCallback();
            }
        };
        return (
            <div className="app">
                <ReactJoyride run={enabled}
                              steps={Tour.STEPS} continuous={true} showSkipButton={true} showProgress={true}
                              callback={callback}
                              styles={{
                                  options: {
                                      primaryColor: '#f9c00c',
                                  },
                                  tooltipContainer: {
                                      textAlign: "justify",
                                  }
                              }}

                              locale={{
                                  next: "Dalej",
                                  last: "Zamknij",
                                  skip: "Pomiń",
                                  back: "Wstecz"
                              }}
                />
            </div>)
    }

}